import { create } from 'zustand';

/**
 *   
 */
export type LoadingState = 'connecting' | 'loadingDesks' | 'ready';

/**
 * Relay   
 */
export interface RelayState {
  /** WebSocket   */
  isConnected: boolean;

  /**    */
  isAuthenticated: boolean;

  /** Relay   ID */
  deviceId: string | null;

  /**      */
  desksLoaded: boolean;

  /**    ( ) */
  loadingState: LoadingState;

  // Actions
  setConnected: (connected: boolean) => void;
  setAuthenticated: (authenticated: boolean) => void;
  setDeviceId: (deviceId: string | null) => void;
  setDesksLoaded: (loaded: boolean) => void;
  reset: () => void;
}

/**
 *  
 */
const initialState = {
  isConnected: false,
  isAuthenticated: false,
  deviceId: null,
  desksLoaded: false,
};

/**
 *   
 */
function computeLoadingState(
  isConnected: boolean,
  isAuthenticated: boolean,
  desksLoaded: boolean
): LoadingState {
  if (!isConnected || !isAuthenticated) {
    return 'connecting';
  }
  if (!desksLoaded) {
    return 'loadingDesks';
  }
  return 'ready';
}

/**
 * Relay    
 *
 * WebSocket , ,   .
 */
export const useRelayStore = create<RelayState>((set, get) => ({
  ...initialState,
  loadingState: 'connecting',

  setConnected: (connected) => {
    if (connected) {
      set({ isConnected: true });
    } else {
      //      
      set({
        isConnected: false,
        isAuthenticated: false,
        deviceId: null,
        desksLoaded: false,
        loadingState: 'connecting',
      });
    }
    //   
    const state = get();
    set({
      loadingState: computeLoadingState(
        connected ? true : false,
        connected ? state.isAuthenticated : false,
        connected ? state.desksLoaded : false
      ),
    });
  },

  setAuthenticated: (authenticated) => {
    set({ isAuthenticated: authenticated });
    const state = get();
    set({
      loadingState: computeLoadingState(
        state.isConnected,
        authenticated,
        state.desksLoaded
      ),
    });
  },

  setDeviceId: (deviceId) => {
    set({ deviceId });
  },

  setDesksLoaded: (loaded) => {
    set({ desksLoaded: loaded });
    const state = get();
    set({
      loadingState: computeLoadingState(
        state.isConnected,
        state.isAuthenticated,
        loaded
      ),
    });
  },

  reset: () => {
    set({
      ...initialState,
      loadingState: 'connecting',
    });
  },
}));
