import { useState } from 'react';
import { Lock, Pencil, AlertTriangle, MoreVertical, RefreshCw, Package, Bug, Type, Trash2 } from 'lucide-react';
import { Button } from '../ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from '../ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '../ui/dialog';

type PermissionMode = 'default' | 'acceptEdits' | 'bypassPermissions';

const PERMISSION_CONFIG: Record<
  PermissionMode,
  { label: string; icon: typeof Lock }
> = {
  default: { label: 'Default', icon: Lock },
  acceptEdits: { label: 'Accept Edits', icon: Pencil },
  bypassPermissions: { label: 'Bypass All', icon: AlertTriangle },
};

const PERMISSION_MODES: PermissionMode[] = [
  'default',
  'acceptEdits',
  'bypassPermissions',
];

interface SessionMenuButtonProps {
  permissionMode?: PermissionMode;
  onPermissionModeChange?: (mode: PermissionMode) => void;
  onNewSession?: () => void;
  onCompact?: () => void;
  onBugReport?: () => void;
  onRename?: () => void;
  onDelete?: () => void;
  conversationName?: string;
}

/**
 *   
 */
export function SessionMenuButton({
  permissionMode = 'default',
  onPermissionModeChange,
  onNewSession,
  onCompact,
  onBugReport,
  onRename,
  onDelete,
  conversationName,
}: SessionMenuButtonProps) {
  const [showNewSessionDialog, setShowNewSessionDialog] = useState(false);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const config = PERMISSION_CONFIG[permissionMode];
  const Icon = config.icon;

  const handlePermissionCycle = () => {
    const currentIndex = PERMISSION_MODES.indexOf(permissionMode);
    const nextIndex = (currentIndex + 1) % PERMISSION_MODES.length;
    const nextMode = PERMISSION_MODES[nextIndex];
    onPermissionModeChange?.(nextMode);
  };

  const handleNewSession = () => {
    setShowNewSessionDialog(true);
  };

  const confirmNewSession = () => {
    setShowNewSessionDialog(false);
    onNewSession?.();
  };

  const confirmDelete = () => {
    setShowDeleteDialog(false);
    onDelete?.();
  };

  return (
    <>
      <div className="flex items-center">
        <Button
          variant="ghost"
          size="icon"
          onClick={handlePermissionCycle}
          title={config.label}
        >
          <Icon className="h-4 w-4" />
        </Button>

        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" size="icon">
              <MoreVertical className="h-4 w-4" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            {onRename && (
              <DropdownMenuItem onClick={onRename}>
                <Type className="mr-2 h-4 w-4" />
                 
              </DropdownMenuItem>
            )}
            <DropdownMenuItem onClick={handleNewSession}>
              <RefreshCw className="mr-2 h-4 w-4" />
               
            </DropdownMenuItem>
            <DropdownMenuItem onClick={onCompact}>
              <Package className="mr-2 h-4 w-4" />
              
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuItem onClick={onBugReport} className="text-destructive">
              <Bug className="mr-2 h-4 w-4" />
               
            </DropdownMenuItem>
            {onDelete && (
              <DropdownMenuItem
                onClick={() => setShowDeleteDialog(true)}
                className="text-destructive"
              >
                <Trash2 className="mr-2 h-4 w-4" />
                 
              </DropdownMenuItem>
            )}
          </DropdownMenuContent>
        </DropdownMenu>
      </div>

      {/*     */}
      <Dialog open={showNewSessionDialog} onOpenChange={setShowNewSessionDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle> </DialogTitle>
            <DialogDescription>
                   ?
              <br />
                 .
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowNewSessionDialog(false)}>
              
            </Button>
            <Button variant="destructive" onClick={confirmNewSession}>
                
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/*     */}
      <Dialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle> </DialogTitle>
            <DialogDescription>
              "{conversationName}"  ?
              <br />
                  .
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowDeleteDialog(false)}>
              
            </Button>
            <Button variant="destructive" onClick={confirmDelete}>
              
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
}
