/**
 *   
 */
export interface CacheStats {
  /**    */
  count: number;

  /**     */
  sizeBytes: number;

  /**    */
  maxSizeBytes: number;

  /**  (%) */
  usagePercent: number;
}

/**
 *  
 */
export interface CacheConfig {
  /**    () */
  maxSizeBytes: number;
}

/**
 *  
 */
interface CacheEntry {
  data: Uint8Array;
  size: number;
}

/**
 *    (LRU)
 *
 *    LRU    .
 */
export class ImageCacheService {
  private cache: Map<string, CacheEntry>;
  private accessOrder: string[];
  private currentSize: number;
  private maxSize: number;

  constructor(config: CacheConfig) {
    this.cache = new Map();
    this.accessOrder = [];
    this.currentSize = 0;
    this.maxSize = config.maxSizeBytes;
  }

  /**
   *   
   */
  get(key: string): Uint8Array | undefined {
    const entry = this.cache.get(key);
    if (!entry) return undefined;

    //   
    this.updateAccessOrder(key);

    return entry.data;
  }

  /**
   *   
   */
  set(key: string, data: Uint8Array): void {
    const size = data.length;

    //    ( )
    if (this.cache.has(key)) {
      this.delete(key);
    }

    //  
    while (this.currentSize + size > this.maxSize && this.accessOrder.length > 0) {
      const oldestKey = this.accessOrder[0];
      this.delete(oldestKey);
    }

    //        
    if (size > this.maxSize) {
      return;
    }

    // 
    this.cache.set(key, { data, size });
    this.accessOrder.push(key);
    this.currentSize += size;
  }

  /**
   *     
   */
  has(key: string): boolean {
    return this.cache.has(key);
  }

  /**
   *   
   */
  delete(key: string): boolean {
    const entry = this.cache.get(key);
    if (!entry) return false;

    this.cache.delete(key);
    this.currentSize -= entry.size;
    this.accessOrder = this.accessOrder.filter((k) => k !== key);

    return true;
  }

  /**
   *   
   */
  clear(): void {
    this.cache.clear();
    this.accessOrder = [];
    this.currentSize = 0;
  }

  /**
   *   
   */
  getStats(): CacheStats {
    return {
      count: this.cache.size,
      sizeBytes: this.currentSize,
      maxSizeBytes: this.maxSize,
      usagePercent: this.maxSize > 0 ? (this.currentSize / this.maxSize) * 100 : 0,
    };
  }

  /**
   *    (LRU)
   */
  private updateAccessOrder(key: string): void {
    const index = this.accessOrder.indexOf(key);
    if (index > -1) {
      this.accessOrder.splice(index, 1);
      this.accessOrder.push(key);
    }
  }
}

/**
 *    (50MB)
 */
export const imageCache = new ImageCacheService({
  maxSizeBytes: 50 * 1024 * 1024,
});
