import { useCallback, useRef, useEffect, useMemo } from 'react';

/**
 * useLongPress  
 */
export interface UseLongPressOptions {
  /**    (ms), : 500 */
  delay?: number;
  /**   (0~1) */
  onProgress?: (progress: number) => void;
  /**   */
  disabled?: boolean;
}

/**
 * useLongPress  
 */
export interface UseLongPressHandlers {
  onMouseDown: (e: React.MouseEvent) => void;
  onMouseUp: (e: React.MouseEvent) => void;
  onMouseLeave: (e: React.MouseEvent) => void;
  onTouchStart: (e: React.TouchEvent) => void;
  onTouchEnd: (e: React.TouchEvent) => void;
  onTouchMove: (e: React.TouchEvent) => void;
}

/**     (ms) */
const DEFAULT_DELAY = 500;

/**    (ms) */
const PROGRESS_INTERVAL = 50;

/**
 * ()  
 *
 *     .
 *       .
 *
 * @param onLongPress     
 * @param options  (delay, onProgress, disabled)
 * @returns   
 */
export function useLongPress(
  onLongPress: () => void,
  options: UseLongPressOptions = {}
): UseLongPressHandlers {
  const { delay = DEFAULT_DELAY, onProgress, disabled = false } = options;

  //  
  const timerRef = useRef<ReturnType<typeof setTimeout> | null>(null);
  const progressTimerRef = useRef<ReturnType<typeof setInterval> | null>(null);
  const startTimeRef = useRef<number>(0);

  //   
  const clearTimers = useCallback(() => {
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    if (progressTimerRef.current) {
      clearInterval(progressTimerRef.current);
      progressTimerRef.current = null;
    }
  }, []);

  //   ( 0 )
  const cancel = useCallback(() => {
    clearTimers();
    if (onProgress) {
      onProgress(0);
    }
  }, [clearTimers, onProgress]);

  //  
  const start = useCallback(() => {
    if (disabled) return;

    startTimeRef.current = Date.now();

    //  
    timerRef.current = setTimeout(() => {
      onLongPress();
      clearTimers();
    }, delay);

    //   
    if (onProgress) {
      progressTimerRef.current = setInterval(() => {
        const elapsed = Date.now() - startTimeRef.current;
        const progress = Math.min(elapsed / delay, 1);
        onProgress(progress);
      }, PROGRESS_INTERVAL);
    }
  }, [disabled, delay, onLongPress, onProgress, clearTimers]);

  //     
  useEffect(() => {
    return () => {
      clearTimers();
    };
  }, [clearTimers]);

  //   
  return useMemo(
    () => ({
      onMouseDown: start,
      onMouseUp: cancel,
      onMouseLeave: cancel,
      onTouchStart: start,
      onTouchEnd: cancel,
      onTouchMove: cancel,
    }),
    [start, cancel]
  );
}
